<script setup>
import { ref, computed } from "vue";
import { useUserStore } from '../../store/user'
import { useToast } from "vue-toastification";
import usersApi from '@/axios/api'
import { useHead } from '@vueuse/head'
import { handleError } from "@/utils/helpers";
const userStore = useUserStore()
const toast = useToast();
const user = computed(() => userStore.getUser)
const adminUrl = process.env.VUE_APP_BASE_URL
let loading = ref(true)
let uploadLoading = ref(true)
let image = ref()
let orders = ref([])
useHead({
    // Can be static or computed
    title: computed(() => `پنل کاربری - ${userStore.getUser.name} ${userStore.getUser.family}`),
})
async function index() {
    loading.value = true;
    try {
        const response = await usersApi.get(`user/panel`);
        loading.value = false
        if (response.data.status == 'success') {
            orders.value = response.data.data.orders

            loading.value = false
        }
    } catch (err) {
        toast.error(handleError(err).join(' '), {
            timeout: 2000
        });
    }
}
index()
function onloadAvatar(e) {
    $('#modal-upload-avatars').modal('show')
    let avatar = document.getElementById('avatar');
    if (avatar) {
        const file = e.currentTarget.files[0]
        image.value = file;
        if (file) {
            document.getElementById('img-prev').src = URL.createObjectURL(file)
        }
    }
}
async function uploadImage() {
    const config = {
        headers: { 'content-type': 'multipart/form-data' }
    }
    let formData = new FormData();
    formData.append('avatar', image.value);

    uploadLoading.value = true;
    try {
        const response = await usersApi.post(`user/panel/upload/avatars`, formData, config)
        if (response.data.status) {
            toast.success(response.data.message, {
                timeout: 2000
            });
            $('#modal-upload-avatars').modal('hide')
        } else {
            toast.warning(response.data.message, {
                timeout: 2000
            });
        }

    } catch (err) {
        toast.error(err.response.data.message, {
            timeout: 2000
        });
    } finally {
        uploadLoading.value = false
    }
}
</script>